<template>
  <div v-if="user" class="user">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>/
      </template>
      <template #content>
        <div>
          <img :src="user.avatar_url" alt />
          <span>{{user.loginname}}</span>
        </div>
        <span>{{user.score}}积分</span>
        <span>
          <a
            target="_blank"
            :href="`http://github.com/${user.githubUsername}`"
          >{{user.githubUsername}}</a>
        </span>
        <div>注册时间{{user.create_at | moment('from','now')}}</div>
      </template>
    </Panel>
    <Panel>
      <template #header>最近创建的话题</template>
      <template #content>
        <PostItem v-for="topic in user.recent_topics" :key="topic.id" v-bind="topic" />
      </template>
    </Panel>
    <Panel>
      <template #header>最近参与的话题</template>
      <template #content>
        <PostItem v-for="topic in user.recent_replies" :key="topic.id" v-bind="topic" />
      </template>
    </Panel>
  </div>
</template>

<script>
import PostItem from "../components/PostItem";
import Panel from "../containers/Panel";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    Panel,
    PostItem
  },
  watch: {
    "$route.params.username": {
      handler(newValue) {
        this.getPostUser(newValue);
      },
      immediate: true
    }
  },
  created() {
    this.getPostUser(this.$route.params.username);
  },
  methods: {
    ...mapActions(["getPostUser"])
  },
  computed: {
    ...mapState({
      user: state => state.user.user
    })
  }
};
</script>

<style lang='less'>
.user {
  .panel {
    .panel-header {
      background-color: #ccc;
    }
  }
}
</style>