<template>
  <header>
    <div class="header-inner">
      <h1>
        <router-link to="/">
          <img src="../assets/123.png" alt />
        </router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <router-link v-if="!info" to="/login">登录</router-link>
        <div v-else>
          <img style="width:30px;height:30px" :src="info.avatar_url" alt />
          <span>退出</span>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState({
      info: state => state.login.info
    })
  },
  methods: {
    ...mapMutations(["getInfo"]),
    logout() {
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
      localStorage.removeItem("info");
      localStorage.removeItem("token");
      this.getInfo(null);
    }
  }
};
</script>

<style lang='less'>
header {
  background-color: #000;
  .header-inner {
    height: 50px;
    display: flex;
    width: 1400px;
    justify-content: space-between;
    margin: 0 auto;
    h1 {
      margin: 0;
      img {
        height: 50px;
        width: 100px;
      }
    }
  }
}
</style>