<template>
  <div v-if="post">
    <Panel>
      <template #header>
        <h2>
          <span class="tag" v-if="post.top || post.good">{{post.top ? '置顶' : '精华'}}</span>
          {{post.title}}
        </h2>
        <p>
          <span>·</span>
          <span>发布于{{post.create_at | moment("from", "now")}}</span>
          <span>·</span>
          <span>作者{{post.author.loginname}}</span>
          <span>·</span>
          <span>{{post.visit_count}}次浏览</span>
          <span>·</span>
          <span>来自{{post.tab | transformTab}}</span>
        </p>
        <el-button
          @click="collect"
          size="mini"
          :type=" post.is_collect ? 'info' : 'success'"
          v-if="info"
        >
          {{
          post.is_collect ? "取消收藏" : "收藏"
          }}
        </el-button>
      </template>
      <template #content>
        <div class="post-content" v-html="post.content"></div>
      </template>
    </Panel>
    <panel>
      <template #header>
        <span>{{post.reply_count}}回复</span>
      </template>
      <template v-if="post.replies.length" #content>
        <PostCommentItem
          v-for="(comment,index) in post.replies"
          :key="comment.id"
          v-bind="{...comment,index}"
        />
      </template>
    </panel>

    <Panel v-if="info">
      <template #header>
        <span>添加回复</span>
      </template>
      <template #content>
        <textarea name id cols="30" rows="10"></textarea>
        <el-button type="primary">回复</el-button>
      </template>
    </Panel>
  </div>
</template>

<script>
import PostCommentItem from "./PostCommentItem";
import Panel from "../containers/Panel";
import { mapActions, mapState } from "vuex";

export default {
  components: {
    Panel,
    PostCommentItem
  },
  computed: {
    ...mapState({
      post: state => state.post.post,
      info: state => state.login.info
    })
  },
  created() {
    this.getPostItem(this.$route.params.id);
  },
  methods: {
    ...mapActions(["getPostItem", "collectHandle"]),
    collect() {
      this.collectHandle({
        id: this.post.id,
        is_collect: !this.post.is_collect
      });
    }
  }
};
</script>

<style lang='less'>
.panel .panel-header {
  background: #fff;
  h2 {
    .tag {
      flex-shrink: 0;
      background: #80bd01;
      padding: 2px 4px;
      border-radius: 3px;
      color: #fff;
      font-size: 12px;
    }
  }
  p {
    font-size: 12px;
    color: #838383;
  }
}
</style>